@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300;400;500;700&display=swap);
.parent{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-column-gap:45px;grid-row-gap:45px;height:80%;padding:50px}.div3{grid-area:2/1/3/4;display:flex;flex-direction:row;align-items:center;justify-content:space-around;align-content:center;background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:20px;border:1px solid rgba(255,255,255,.18)}.div3 div{flex:1 1;display:flex;flex-direction:column;align-items:center;justify-content:space-around;height:100%}.div1{grid-area:1/1/2/2;display:flex;flex-direction:column;align-items:center;justify-content:space-around;background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:20px;border:1px solid rgba(255,255,255,.18)}.div2{grid-area:1/2/2/4;background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:20px;border:1px solid rgba(255,255,255,.18);display:flex;flex-direction:column;padding:30px;grid-gap:40px;gap:40px}.infoGrid__todayHeader{display:flex;justify-content:space-between}.infoGrid__hoursForecast{flex:1 1;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.infoGrid__hoursForecast div{flex:1 1;display:flex;flex-direction:column;align-items:center;justify-content:space-around;height:100%}@media(max-width: 900px){.parent{grid-template-columns:repeat(1, 1fr);grid-template-rows:repeat(3, 1fr);padding:0 20px}.div1,.div2,.div3{grid-area:auto;padding:20px}}*{margin:0;padding:0;font-family:"Readex Pro",sans-serif;color:#fff}body{height:100vh;width:100vw;background:#1d1d1d}span{font-size:2.5rem}img{width:90px}p{font-size:2rem}header{display:flex;align-content:center;align-items:center;justify-content:space-around;grid-gap:25px;gap:25px;padding:20px}header form{flex:1 1;display:flex;justify-content:space-around;grid-gap:20px;gap:20px}header form input{flex:1 1;background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:10px;border:1px solid rgba(255,255,255,.18);padding:10px}header form button{-webkit-backdrop-filter:blur(8px);background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:10px;border:1px solid rgba(255,255,255,.18);padding:10px}header form button:hover{background:rgba(255,255,255,.5);cursor:pointer}#root,.App{height:100%}.app__ball1{position:fixed;z-index:-1;width:600px;height:600px;border-radius:100%;top:-40%;right:-30%;background:#3b8ea5;box-shadow:0px 0px 5px 100px #3b8ea5}.app__ball2{position:fixed;z-index:-1;width:800px;height:800px;border-radius:100%;bottom:-30%;right:-30%;background:#f49e4c;box-shadow:0px 0px 5px 100px #f49e4c}.app__ball3{position:fixed;z-index:-1;width:300px;height:300px;border-radius:100%;bottom:0%;left:-15%;background:#935fa7;box-shadow:0px 0px 5px 100px #935fa7}.app__ball4{position:fixed;z-index:-1;width:900px;height:900px;border-radius:100%;top:-45%;left:-50%;background:#ed6767;box-shadow:0px 0px 5px 100px #ed6767}.app__loading{position:absolute;width:80%;text-align:center;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-backdrop-filter:blur(8px);background:rgba(255,255,255,.35);box-shadow:0 8px 32px 0 rgba(0,0,0,.37);backdrop-filter:blur(20px);-webkit-backdrop-filter:blur(20px);border-radius:10px;border:1px solid rgba(255,255,255,.18);padding:20px}@media(max-width: 900px){h1{font-size:1.5rem}span{font-size:1.5rem}p{font-size:1rem}img{width:100%;max-width:70px}.app__title{display:none}}
